	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"The BEAM, the Erlang VM, was designed to implement Erlang. This has led to that the features it provides and its limitations are different from most other VMs. In this talk we will why the BEAM looks like it does and how this affects the implementation of languages on it. We will also show the Erlang compiler workflow and how to use this when implementing languages. Finally we will show examples of how to \"go beyond\" the BEAM and implement features for which it was wasn't originally designed and the costs this can entail.\n\nAbout Robert:\n\nWhile at Ericsson AB, Robert was one of the original members of the Ericsson Computer Science Lab, and co-inventor of the Erlang language. He took part in the original system design and contributed much of the original libraries, as well as to the current compiler.\n\nWhile at the lab he also did a lot of work on the implementation of logic and functional languages and on garbage collection. He has also worked as an entrepreneur and was one of the co-founders of one of the first Erlang startups (Bluetail). Robert also worked a number of years at the Swedish Defence Materiel Administration (FMV) Modelling and Simulations Group. He co-authored the first book (Prentice-Hall) on Erlang, and is regularly invited to teach and present throughout the world. at present, he is the Principal Language Expert at Erlang Solutions Ltd.","meta":{"duration":"PT1H47M47S"},"presenter":{"name":"Robert Virding"},"source":{"id":"qm0mbQbc9Kc","provider":"youtube"},"slug":"implementing-languages-on-the-beam","tag":["Erlang","BEAM"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/qm0mbQbc9Kc/default.jpg","height":90,"width":120}},"title":"Implementing Languages on the BEAM"}}]}},"pathContext":{"slug":"beam","tag":"BEAM"}}

